.pj__about {
  font-family: var(--font-family);
  background: var(--color-bg);
}
.about-me {
  padding: 0rem 0rem 3rem 0rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.about-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex: 2;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.about-heading {
  color: var(--color-heading);
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  padding: 3rem 0rem;
}
.image {
  display: flex;
  align-items: center;
  justify-content: center;
}
.image img {
  height: 200px;
  width: 200px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 2rem;
  align-self: center;
}

.about-content p {
  font-size: 1.2rem;
  color: #f0f0e6;
  padding: 0 2.3rem;
  text-align: center;
}

.work-arrow {
  margin: 2rem 0;
}

.work-arrow a {
  font-size: 1rem;
  text-decoration: none;
  color: var(--color-subtext);
}

.work-arrow a:hover {
  color: var(--color-heading);
}

.skills-bar {
  padding: 25px 0px 25px 0rem;
}

.skills-bar .bar {
  margin: 25px 0;
}

.skills-bar .bar .info span {
  font-size: 1rem;
  font-weight: 500;
}

.skills-bar .bar .info {
  margin-bottom: 10px;
  color: var(--color--text);
}

.skills-bar .bar .progress-line {
  position: relative;
  height: 10px;
  width: 535px;
  background: var(--color-bar);
  border-radius: 10px;
}

.bar .progress-line span {
  position: absolute;
  background: var(--color-subcontrast);
  height: 100%;
  border-radius: 10px;
  transform: scaleX(0);
  transform-origin: left;
  animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}

@keyframes animate {
  100% {
    transform: scaleX(1);
  }
}

.progress-line .java {
  width: 65%;
}
.progress-line .html {
  width: 80%;
}
.progress-line .css {
  width: 70%;
}
.progress-line .bootstrap {
  width: 50%;
}
.progress-line .javascript {
  width: 55%;
}
.progress-line .c {
  width: 48%;
}
.progress-line .cplus {
  width: 52%;
}
.progress-line .php {
  width: 49%;
}
.progress-line .react {
  width: 55%;
}
.progress-line .mysql {
  width: 43%;
}
.progress-line .python {
  width: 45%;
}

.bar .progress-line span::before {
  position: absolute;
  content: "";
  height: 0;
  right: 0;
  top: -12px;
  width: 0;
  border: 10px solid transparent;
  border-bottom-width: 0px;
  border-right-width: 0px;
  border-top-style: white;
  border-top-color: white;
}

.bar .progress-line span::after {
  position: absolute;
  right: 0;
  top: -28px;
  color: black;
  font-size: 12px;
  font-weight: 700;
  background: #f0f0f0;
  padding: 3px 8px;
  border-radius: 3px;
}

.progress-line .java::after {
  content: "65%";
}
.progress-line .html::after {
  content: "80%";
}
.progress-line .css::after {
  content: "70%";
}
.progress-line .bootstrap::after {
  content: "50%";
}
.progress-line .javascript::after {
  content: "55%";
}
.progress-line .c::after {
  content: "48%";
}
.progress-line .cplus::after {
  content: "52%";
}
.progress-line .php::after {
  content: "49%";
}
.progress-line .react::after {
  content: "55%";
}
.progress-line .mysql::after {
  content: "43%";
}
.progress-line .python::after {
  content: "45%";
}

@media screen and (max-width: 2560px) and (min-width: 1900px) {
  .about-me{
    gap:3rem;
  }
  .about-heading{
    font-size:4rem;
  }
  .image img {
    height: 350px;
    width: 350px;
    margin-bottom: 3rem;
  }
  .about-content p{
    font-size:1.75rem;
  }
  .work-arrow a {
    font-size: 1.5rem;
  }
  .skills-bar .bar .info span{
    font-size:1.75rem;
  }
  .skills-bar .bar .info {
    margin-bottom: 25px;
  }
  .skills-bar .bar .progress-line {
    height: 15px;
    width: 1075px;
  }
  .bar .progress-line span::after {
    top: -40px;
    font-size: 21px;
  }
}
