:root{
  --color-bg-work:#191D882d;
  --color-work-head:var(--color-text);
  --color-work-h: #2257BF;
  --color-work-t:#7863B3;
  --color-view:#4E5E80;
}
.pj__works {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.pj__works-title {
  align-self: flex-start;
}
.pj__works-title h1 {
  text-align: left;
  color: var(--color-work-head);
  font-family: var(--font-family);
  padding: 2rem 0rem;
}
a {
  text-decoration: none;
  width: 100%;
}
.pj__works-box {
  display: flex;
  flex-direction: row;
  background: var(--color-bg-work);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  font-family: var(--font-family);
  height: 300px;
  width: 100%;
  border-radius: 15px;
}

.work-content {
  flex: 0.75;
  padding-right: 1.5rem;
}
.work-content h1 {
  color: var(--color-work-h);
  font-size: 1.5rem;
  padding: 3rem 0rem 1rem 3rem;
}

.work-content p {
  color: var(--color-work-t);
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5rem;
  padding: 0rem 0rem 1rem 3rem;
}

.work-content h6
{
  color: var(--color-view);
  font-size: 0.8rem;
  font-weight: 600;
  line-height: 1.5rem;
  padding: 0rem 0rem 0.5rem 3rem;
}
.work-img {
  flex: 0.25;
  display: flex;
  width: 300px;
  height: 300px;
  justify-content: flex-end;
  padding-right: 0.75rem;
  object-fit: contain;
}

