@media screen and (max-width: 550px) /*Media query for Mobile devices*/ {
  .pj__works-title h1 {
    font-size: 0.7rem;
    padding: 0rem 0rem;
  }
  .pj__works-box {
    height: 100px;
  }
  .work-content h1 {
    font-size: 0.4rem;
    line-height: 0.5rem;
    padding: 1rem 0rem 0.25rem 1rem;
  }
  .work-content p {
    font-size: 0.3rem;
    line-height: 0.4rem;
    padding: 0rem 0rem 0rem 1rem;
  }
  .work-img {
    width: 180px;
    height: 100px;
    padding-right: 0.25rem;
  }
}
